<style scoped>
.content {
    margin-left: 250px;
    transition: margin-left 0.3s;
}
.content.expanded {
    margin-left: 0;
}

.fade-in {
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
}

.fade-in.active {
    opacity: 1;
}
</style>

<template>
    <div class="p-4 mt-5">
        <!-- top button  -->
        <div
            class="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pt-3 pb-2 mb-3 border-bottom"
        >
            <h1 class="h2">{{ RouteUrlList.DocTypeManagement.label }}</h1>
            <div class="btn-toolbar justify-content-end mb-3" role="toolbar" aria-label="Toolbar with button groups">
                <div class="btn-group me-2" role="group" aria-label="First group">
                    <!-- 新增搜尋輸入框 -->
                    <div class="input-group ms-3" style="max-width: 200px">
                        <input
                            type="text"
                            class="form-control"
                            placeholder="搜尋文件名稱"
                            v-model="DocTypeData.searchQuery"
                        />
                        <div
                            class="btn-close position-absolute end-0 top-50 translate-middle cursor-pointer shadow-none"
                            @click="clearSearch"
                            aria-label="清除搜尋"
                        >
                            <button class="btn btn-link p-0" aria-label="Close"></button>
                        </div>
                    </div>
                    <button class="btn btn-sm btn-esun-outline ml-3" @click="throttledHandleSearch">搜尋</button>
                    <button class="btn btn-esun-primary ml-3" @click="open">新增文件類型</button>
                </div>
                <JsonFormModal
                    v-if="createModal.showModal"
                    :myModal="createModal"
                    :modelValue="data"
                    @submit="handleSubmit"
                    @cancel="createModal.showModal = false"
                />

                <!-- 切換顯示模式 -->
                <div class="btn-group" role="group">
                    <button
                        class="btn btn-esun-outline"
                        :class="{ active: DocTypeData.displayMode === 'card' }"
                        @click="DocTypeData.displayMode = 'card'"
                    >
                        卡片模式
                    </button>
                    <button
                        class="btn btn-esun-outline"
                        :class="{ active: DocTypeData.displayMode === 'table' }"
                        @click="DocTypeData.displayMode = 'table'"
                    >
                        表格模式
                    </button>
                </div>
            </div>
        </div>

        <!-- 顯示資料 -->
        <Pagination
            v-if="DocTypeData.totalItems"
            v-model:modelValue="DocTypeData.pageNumber"
            v-model:pageSize="DocTypeData.pageSize"
            :total-items="DocTypeData.totalItems"
        />
        <div
            v-if="DocTypeData.displayMode === 'card'"
            class="row row-cols-1 row-cols-md-2 row-cols-lg-3 g-3 active"
            :class="{ 'fade-in': DocTypeData.pageNumber }"
            ref="dataContainer"
        >
            <div class="col" v-for="item in DocTypeData.DocTypeDataList" :key="item.docType">
                <div class="card card-esun h-100 esun-animate-in">
                    <div class="card-body">
                        <h5 class="card-title">{{ item.docName }}</h5>
                        <p class="card-text">
                            <strong>文件類型代碼:</strong> {{ item.docType }}<br />
                            <strong>描述:</strong> {{ item.description || '-' }}<br />
                            <strong>權限:</strong> {{ permissionLabel(item.permission) }}<br />
                            <strong>是否軟刪除:</strong>
                            <span :class="item.isDeleted ? 'text-success' : 'text-danger'">
                                {{ item.isDeleted ? '是' : '否' }} </span
                            ><br />
                            <strong>建立時間:</strong> {{ Common.FormatDate(item.createdTime) }}<br />
                            <strong>更新時間:</strong> {{ Common.FormatDate(item.lastUpdate) }}
                        </p>
                        <!-- 按鈕群組 -->
                        <div class="dropdown mt-3">
                            <button
                                class="btn btn-esun-outline dropdown-toggle w-100"
                                type="button"
                                data-bs-toggle="dropdown"
                                aria-expanded="false"
                            >
                                <i class="bi bi-gear me-1"></i> 操作選單
                            </button>
                            <ul class="dropdown-menu w-100">
                                <li>
                                    <a class="dropdown-item" href="#" @click.prevent="handleEdit(item)">
                                        <i class="bi bi-pencil-square me-1 text-primary"></i> 修改
                                    </a>
                                </li>
                                <li v-if="!item.isDeleted">
                                    <a class="dropdown-item" href="#" @click.prevent="handleDelete(item)">
                                        <i class="bi bi-trash me-1 text-danger"></i> 刪除
                                    </a>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div
            v-else
            :class="{ 'fade-in': DocTypeData.pageNumber }"
            ref="dataContainer"
            style="overflow: auto"
            class="active"
        >
            <table class="table table-bordered table-hover align-middle">
                <thead class="table-light">
                    <tr>
                        <th>文件類型代碼</th>
                        <th>文件名稱</th>
                        <th>描述</th>
                        <th>權限</th>
                        <th>是否軟刪除</th>
                        <th>建立時間</th>
                        <th>更新時間</th>
                        <th>操作選單</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in DocTypeData.DocTypeDataList" :key="item.docType">
                        <td>{{ item.docType }}</td>
                        <td>{{ item.docName }}</td>
                        <td>{{ item.description || '-' }}</td>
                        <td>{{ permissionLabel(item.permission) }}</td>
                        <td>
                            <span :class="item.isDeleted ? 'text-success' : 'text-danger'">
                                {{ item.isDeleted ? '是' : '否' }}
                            </span>
                        </td>
                        <td>{{ Common.FormatDate(item.createdTime) }}</td>
                        <td>{{ Common.FormatDate(item.lastUpdate) }}</td>
                        <td>
                            <!-- 操作按鈕 -->
                            <div class="dropdown">
                                <button
                                    class="btn dropdown-toggle w-100"
                                    type="button"
                                    data-bs-toggle="dropdown"
                                    aria-expanded="false"
                                >
                                    <i class="bi bi-gear me-1"></i> 操作選單
                                </button>
                                <ul class="dropdown-menu w-100">
                                    <li>
                                        <a class="dropdown-item" href="#" @click.prevent="handleEdit(item)">
                                            <i class="bi bi-pencil-square me-1 text-primary"></i> 修改
                                        </a>
                                    </li>
                                    <li v-if="!item.isDeleted">
                                        <a class="dropdown-item" href="#" @click.prevent="handleDelete(item)">
                                            <i class="bi bi-trash me-1 text-danger"></i> 刪除
                                        </a>
                                    </li>
                                </ul>
                            </div>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>

        <JsonFormModal
            v-if="updateModal.showModal"
            :myModal="updateModal"
            v-model="updateData"
            @submit="handleUpdateSubmit"
            @cancel="updateModal.showModal = false"
        />

        <Pagination
            v-if="DocTypeData.totalItems"
            v-model:modelValue="DocTypeData.pageNumber"
            v-model:pageSize="DocTypeData.pageSize"
            :total-items="DocTypeData.totalItems"
        />
    </div>
</template>

<script setup lang="ts">
import { ref, onMounted, watch, reactive } from 'vue';
defineProps<{ collapsed: boolean; paginationType?: 'client' | 'server' }>();
import Pagination from '@/components/pagination/Pagination.vue';
import * as Common from '@/libs/Common';
import type { DocTypeDTO } from '@/models/DTO/DocType';
import * as ApiClient from '@/libs/ApiClient';
import API from '@/api/v1/';
import JsonFormModal from '@/components/modal/JsonFormModal/JsonFormModal.vue';
import { GetCreateDocTypeModal, GetDefaultFormData, type FileFormData } from './CreateDocTypeModal';
import type { MyModal } from '@/components/modal/JsonFormModal/schema';
import { RouteUrlList } from '@/libs/RouteList';
import {
    SimplePopUp,
    SimplePopUpBtnType,
    SimplePopUpModalType,
    SimplePopUpSize,
    type SimplePopupResolveResult
} from '@/libs/SimplePopUp';

// docType 資料（ API 傳入）
interface IPageData {
    // 去查詢後端資料
    paginationType: 'server';
    DocTypeDataList: DocTypeDTO[];
    totalItems: number;

    pageNumber: number;
    pageSize: number;
    displayMode: 'card' | 'table';
    searchQuery: string;
    createdTime: string;
    lastUpdate: string;
}

const DocTypeData = reactive<IPageData>({
    paginationType: 'server',
    DocTypeDataList: [],
    totalItems: 0,
    pageNumber: 1,
    pageSize: 10,
    displayMode: 'card',
    searchQuery: '',
    createdTime: '',
    lastUpdate: ''
});

// 建立 DocType 相關
const createModal = ref<MyModal>(GetCreateDocTypeModal());
const data = ref<FileFormData>(GetDefaultFormData());

// SimplePopUp 實例
const simplePopUp = new SimplePopUp();

// 修改 DocType 相關
import { GetUpdateDocTypeModal } from './UpdateDocTypeModal';
const updateModal = ref<MyModal>(GetUpdateDocTypeModal());
const updateData = ref<DocTypeDTO>({
    docType: 0,
    docName: '',
    description: null,
    permission: '0',
    isDeleted: false,
    createdTime: '',
    lastUpdate: ''
});
/**
 * @todo 載入資料
 */
const LoadViewData = async () => {
    try {
        const response = await API.DocTypeManagementViewData(
            DocTypeData.pageNumber,
            DocTypeData.pageSize,
            DocTypeData.searchQuery
        );

        if (response.status && response.result?.items) {
            DocTypeData.DocTypeDataList = response.result.items;
            DocTypeData.totalItems = response.result.totalCount;
        } else {
            ApiClient.HandleGeneralRespError(response);
        }
    } catch (error) {
        ApiClient.HandleError(error);
        console.error('Error fetching docTypes:', error);
        // Handle error (e.g., show an error message)
    }
};

onMounted(async () => {
    await LoadViewData();
});

watch(
    () => ({
        pageNumber: DocTypeData.pageNumber,
        pageSize: DocTypeData.pageSize
    }),
    () => {
        LoadViewData();
    }
);

function open() {
    createModal.value.showModal = true;
}

async function handleSubmit(formData: Record<string, any>) {
    const docTypeData = formData as DocTypeDTO;

    try {
        const response = await API.DocTypeManagementCreate(docTypeData);
        if (response.status) {
            simplePopUp
                .showAlert(SimplePopUpModalType.Success, '成功', `文件類型 "${docTypeData.docName}" 已成功建立。`, [
                    {
                        id: 'ok',
                        type: SimplePopUpBtnType.callback,
                        btnClass: 'btn-success',
                        resolveParam: 'ok',
                        title: '確定'
                    }
                ])
                .then(async () => {
                    await LoadViewData();
                });
        } else {
            ApiClient.HandleGeneralRespError(response);
        }
    } catch (error) {
        ApiClient.HandleError(error);
    }

    createModal.value.showModal = false;
}

// 權限顯示
function permissionLabel(permission: string) {
    switch (permission) {
        case '0':
            return '公開';
        case '1':
            return '保護';
        case '2':
            return '內部保護';
        case '3':
            return '私人';
        default:
            return '-';
    }
}

// 搜尋文件名稱與描述
const handleSearch = () => {
    if (!DocTypeData.searchQuery) {
        return;
    }
    DocTypeData.pageNumber = 1; // 重置頁碼
    LoadViewData();
};
// 使用節流函式包裝input事件處理器，提高效能
const throttledHandleSearch = Common.Throttle(handleSearch, 100);

const clearSearch = () => {
    DocTypeData.searchQuery = '';
    DocTypeData.pageNumber = 1; // 重置頁碼
    LoadViewData();
};

// 編輯功能
function handleEdit(item: DocTypeDTO) {
    updateData.value = { ...item };
    updateModal.value.showModal = true;
}

// 更新提交
async function handleUpdateSubmit(formData: Record<string, any>) {
    const docTypeData = formData as DocTypeDTO;

    let action: SimplePopupResolveResult<string> = await simplePopUp.showAlert(
        SimplePopUpModalType.Warning,
        '警告',
        `確定要更新文件類型 "${docTypeData.docName}" 嗎？`,
        [
            {
                id: 'cancel',
                type: SimplePopUpBtnType.callback,
                btnClass: 'btn-esun-outline',
                resolveParam: 'cancel',
                title: '取消'
            },
            {
                id: 'confirm',
                type: SimplePopUpBtnType.callback,
                btnClass: 'btn-esun-primary',
                resolveParam: 'confirm',
                title: '確定'
            }
        ]
    );

    if (action.param === 'confirm') {
        try {
            const response = await API.DocTypeManagementUpdate(docTypeData);
            if (response.status) {
                simplePopUp
                    .showAlert(SimplePopUpModalType.Success, '成功', `文件類型 "${docTypeData.docName}" 已成功更新。`, [
                        {
                            id: 'ok',
                            type: SimplePopUpBtnType.callback,
                            btnClass: 'btn-success',
                            resolveParam: 'ok',
                            title: '確定'
                        }
                    ])
                    .then(async () => {
                        await LoadViewData();
                    });
            } else {
                ApiClient.HandleGeneralRespError(response);
            }
        } catch (error) {
            ApiClient.HandleError(error);
        }
    }

    updateModal.value.showModal = false;
}

// 刪除功能
async function handleDelete(item: DocTypeDTO) {
    let action: SimplePopupResolveResult<string> = await simplePopUp.showAlert(
        SimplePopUpModalType.Warning,
        '警告',
        `確定要刪除文件類型 "${item.docName}" 嗎？此操作將進行軟刪除。`,
        [
            {
                id: 'cancel',
                type: SimplePopUpBtnType.callback,
                btnClass: 'btn-esun-outline',
                resolveParam: 'cancel',
                title: '取消'
            },
            {
                id: 'confirm',
                type: SimplePopUpBtnType.callback,
                btnClass: 'btn-danger',
                resolveParam: 'confirm',
                title: '確定刪除'
            }
        ]
    );

    if (action.param === 'confirm') {
        try {
            const response = await API.DocTypeManagementDelete(item.docType);
            if (response.status) {
                simplePopUp
                    .showAlert(SimplePopUpModalType.Success, '成功', `文件類型 "${item.docName}" 已成功刪除。`, [
                        {
                            id: 'ok',
                            type: SimplePopUpBtnType.callback,
                            btnClass: 'btn-success',
                            resolveParam: 'ok',
                            title: '確定'
                        }
                    ])
                    .then(async () => {
                        await LoadViewData();
                    });
            } else {
                ApiClient.HandleGeneralRespError(response);
            }
        } catch (error) {
            ApiClient.HandleError(error);
        }
    }
}
</script>
