import type { MyModal } from '@/components/modal/JsonFormModal/schema';

export const GetUpdateDocTypeModal = (): MyModal => {
    return {
        modalId: 'updateDocTypeModal',
        title: '修改文件類型',
        showModal: false,
        size: 'modal-xl',
        staticBackdrop: true,
        fieldGroups: [
            {
                title: '',
                gridClass: 'col-12',
                fields: [
                    {
                        modelId: 'docType',
                        name: 'docType',
                        type: 'text',
                        label: '文件類型代碼',
                        placeholder: '請輸入文件類型代碼',
                        required: true,
                        maxlength: 1,
                        disabled: true
                    },
                    {
                        modelId: 'docName',
                        name: 'docName',
                        type: 'text',
                        label: '文件名稱',
                        placeholder: '請輸入文件名稱',
                        required: true,
                        maxlength: 50
                    },
                    {
                        modelId: 'description',
                        name: 'description',
                        type: 'text',
                        label: '描述',
                        placeholder: '請輸入描述',
                        required: false,
                        maxlength: 100
                    },
                    {
                        modelId: 'permission',
                        name: 'permission',
                        type: 'select',
                        label: '權限',
                        required: true,
                        options: [
                            { label: '公開', value: '0' },
                            { label: '保護', value: '1' },
                            { label: '內部保護', value: '2' },
                            { label: '私人', value: '3' }
                        ]
                    },
                    {
                        modelId: 'isDeleted',
                        name: 'isDeleted',
                        type: 'radio',
                        label: '是否刪除',
                        inline: true,
                        options: [
                            { label: '刪除', value: true },
                            { label: '不刪除', value: false }
                        ],
                        required: true
                    }
                ]
            }
        ]
    };
};
