import type { MyModal } from '@/components/modal/JsonFormModal/schema';

export const GetCreateDocTypeModal = (): MyModal => {
    return {
        modalId: 'docTypeModal',
        title: '新增文件類型',
        showModal: false,
        size: 'modal-xl',
        staticBackdrop: true,
        fieldGroups: [
            {
                title: '',
                gridClass: 'col-12',
                fields: [
                    {
                        modelId: 'permission',
                        name: 'permission',
                        type: 'radio',
                        label: '文件類別權限',
                        inline: true,
                        options: [
                            { label: '不限制任何系統 (Public)', value: '0' },
                            { label: '限制註冊系統 (Protect)', value: '1' },
                            { label: '限制白名單系統 (ProtectInternal)', value: '2' },
                            { label: '自己才可存取 (Private)', value: '3' }
                        ],
                        required: true
                    },
                    {
                        modelId: 'docName',
                        name: 'docName',
                        type: 'text',
                        label: '文件名稱',
                        placeholder: '請輸入文件名稱',
                        required: true,
                        maxlength: 50
                    },
                    {
                        modelId: 'description',
                        name: 'description',
                        type: 'text',
                        label: '描述',
                        placeholder: '請輸入描述',
                        required: false,
                        maxlength: 100
                    }
                ]
            }
        ]
    };
};

export interface FileFormData {
    docType: number | null;
    docName: string;
    description: string | null;
    permission: string;
}
export const GetDefaultFormData = (): FileFormData => {
    return {
        docType: null,
        docName: '',
        description: null,
        permission: '0'
    };
};
